import React from 'react'
import { Grid, Image, Container } from 'semantic-ui-react'
import Simple from '../../components/simple.js'
import img from '../../data/img/no-oauth.png'
export default (props) => (
  <Simple
    title={'Our Tips'}
    description={
      <Container>
        <p> However, many apps do not offer the option to log in with a trusted service </p>
      </Container>
    }
    to='/recommendation/pm/'
    buttonText={'Next'}
  >
    <Grid.Row>
      <Grid.Column width={7}>
        <Image src={img} fluid />
      </Grid.Column>
    </Grid.Row>
  </Simple>
)



